@import '../utils/vars.scss';
@import '../utils/mixins.scss';

.instagram {
  position: relative;
  background: $bgColorInsta;
  margin-bottom: 220px;

  &__icon {
    position: absolute;
    left: 480px;

    @include onPhone {
      display: none;
    }
  }

  &__title {
    padding-top: 170px;
    padding-left: 375px;

    @include onPhone {
      padding: 80px 0 20px 20px;
    }
  }

  &__blocks {
    display: flex;
    max-width: 1970px;
    width: 100%;
    gap: 30px;
  }
}
