@import '../src/components/utils/mixins.scss';
@import '../src/components/utils/vars.scss';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  scroll-behavior: smooth;
}

body {
  min-width: 390px;
  max-width: 1920px;
  margin: 0 auto;
}

.maintitle {
  padding-bottom: 60px;
  font-family: 'Montserrat-Bold', sans-serif;
  font-size: $titleSize;
  font-weight: bold;
}

.confetti {
  @include onDesktop {
    display: none;
  }
}

.blue__button {
  padding: 20px 35px;
  color: $whiteColor;
  background-color: $mainBlueColor;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-family: 'Montserrat-Bold', sans-serif;
  font-size: $smallSize;
  transition: all 0.4s;

  &__sponsor {
    display: flex;
  }

  &:hover {
    box-shadow: $blueShadowColor;
  }
}

