@import '../../utils/vars.scss';
@import '../../utils/mixins.scss';

.posts {
  max-width: 370px;
  width: 100%;

  @include onPhone {
    min-width: 345px;
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
  }

  &__active {
    display: flex;
    align-items: center;
    gap: 40px;
    padding: 20px 0 8px 0;
    

    @include onPhone {
      padding-left: 10px;
    }
  }

  &__activ {
    display: flex;
    gap: 8px;
    align-items: center;
    font-weight: bold;
    font-family: 'Montserrat-Bold', sans-serif;
    font-size: $smallSize;
  }

  &__title {
    padding-bottom: 8px;
    font-size: $smallSize;
    color: $mainBlackColor;
    font-weight: normal;
    font-family: 'Raleway-VariableFont_wght', sans-serif;

    @include onPhone {
      padding-left: 10px;
    }
  }

  &__tags {
    max-width: 370px;
    width: 100%;
    color: $mainBlackColor;
    font-size: $smallSize;
    opacity: 0.5;
    font-family: 'Raleway-VariableFont_wght', sans-serif;
  }
}
