@import '../utils/vars.scss';
@import '../utils/mixins.scss';
@import '../../fonts.scss';

.about {
  display: flex;
  align-items: center;
  max-width: 1270px;
  width: 100%;
  gap: 30px;
  margin: 0 auto;
  margin-bottom: 120px;
  position: relative;

  @include onPhone {
    margin-bottom: 80px;
  }

  @include onTablet {
    flex-direction: column;
    max-width: 530px;
    text-align: center;
  }

  &__content {
    @include onTablet {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__subtitle {
    padding-bottom: 80px;
    max-width: 570px;
    opacity: $opacityCoef;
    font-size: $smallSize;
    font-family: 'Montserrat-Regular', sans-serif;
    line-height: 24px;

    @include onPhone {
      padding-bottom: 40px;
    }
  }

  &__arrow {
   margin-left: 12px;
  }

  &__image {
    max-width: 530px;
    width: 100%;
  }

  
  &__confetti {
    position: absolute;
    right: 1200px;
    top: 340px;
  }
}
