@mixin onMini {
  @media (max-width: 345px) {
    @content;
  }
}

@mixin onMiniPhone {
  @media (max-width: 450px) {
    @content;
  }
}

@mixin onPhone {
  @media (max-width: 815px) {
    @content;
  }
}

@mixin onTablet {
  @media (max-width: 1220px) {
    @content;
  }
}

@mixin onDesktop {
  @media (max-width: 1510px) {
    @content;
  }
}
